import { useState, useEffect } from 'react'
import CotationCSS from './Cotation.module.css'

function Cotation() {
    let [dollar, setDollar] = useState([])
    let [euro, setEuro] = useState([])
    let [loading, setLoading] = useState(true)

    const olindaPrefix = "https://olinda.bcb.gov.br/olinda/servico/PTAX/versao/v1/odata"
    //Usando moeda por período, e não do dia:
    const dolarCotationURL = `${olindaPrefix}/CotacaoMoedaPeriodo(moeda=@moeda,dataInicial=@dataInicial,dataFinalCotacao=@dataFinalCotacao)?@moeda='USD'&@dataInicial='10-10-2021'&@dataFinalCotacao='10-11-2021'&$top=100&$format=json`
    const euroCotationURL = `${olindaPrefix}/CotacaoMoedaPeriodo(moeda=@moeda,dataInicial=@dataInicial,dataFinalCotacao=@dataFinalCotacao)?@moeda='EUR'&@dataInicial='10-10-2021'&@dataFinalCotacao='10-11-2021'&$top=100&$format=json`

    useEffect(() => {
        async function fetchData(urlToBeFetched, dataToBeSet) {
            let response = await fetch(urlToBeFetched)
            let data = await response.json()
            dataToBeSet(data.value)
        }
        try {
            fetchData(dolarCotationURL, setDollar)
            fetchData(euroCotationURL, setEuro)
        }
        catch (e) {
            console.log(e)
        }
        finally {
            setLoading(false)
        }
    }, [])

    if (loading === true) {
        return (
            <div>
                <h4>Loading...</h4>
            </div>
        )
    }
    else {
        return (
            <div>
                <h2 className={CotationCSS.boldText}>Cotação</h2>
                <div className={CotationCSS.cotation}>
                    <Currency currency='Dólar EUA' purchase={dollar && dollar.map((item) => item.cotacaoCompra)}
                        selling={dollar && dollar.map((item) => item.cotacaoVenda)} />
                    <Currency currency='Euro' purchase={euro && euro.map((item) => item.cotacaoCompra)}
                        selling={euro && euro.map((item) => item.cotacaoVenda)} />
                </div>
            </div>
        )
    }
}

function Currency(props) {
    return (
        <div className={CotationCSS.currencyList}>

            <div className={CotationCSS.column}>
                <p className={CotationCSS.boldText}>{props.currency}</p>
                <p>15/11 (PTAX)</p>
                <p>16/11 13:00</p>
            </div>

            <div className={CotationCSS.column}>
                <p className={CotationCSS.boldText}>Compra (R$)</p>
                {props.purchase.map((item, index) => index >= 3 ? <p key={item}>{item.toFixed(4)}</p> : null)}
            </div>

            <div className={CotationCSS.column}>
                <p className={CotationCSS.boldText}>Venda (R$)</p>
                {props.selling.map((item, index) => index >= 3 ? <p key={item}>{item.toFixed(4)}</p> : null)}
            </div>

        </div>
    )
}

export default Cotation;