import React, { useState, useEffect } from 'react'
import BottomComponentCSS from "./BottomComponent.module.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronRight, faRss, fa } from "@fortawesome/free-solid-svg-icons";

function BottomComponent() {
    return (
        <div className={BottomComponentCSS.bottomcomponent}>
            <BottomItems />
            <BottomItems />
            <BottomItems />
            <BottomItems />
            <BottomItems />
            <BottomItems />
            <BottomItems />
        </div>
    )
}

function BottomItems() {
    return (
        <a href="#" className={BottomComponentCSS.linkBox}>
            <div className={BottomComponentCSS.icon}>
                <FontAwesomeIcon icon={faChevronRight} />
            </div>
            <div className={BottomComponentCSS.text}>
                Agenda BC#
            </div>
        </a>
    )
}


export default BottomComponent
